import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { Subscription, firstValueFrom } from 'rxjs';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { ChqWidgetsDropdownModel } from 'src/app/model/chq-widgets-dropdown-model';
import { ChqWidgetsInputModel, IControlValue } from 'src/app/model/chq-widgets-input-model';
import { CommonService, i18nInfo } from 'src/app/services/common/common.service';
import { MonitorService } from 'src/app/services/monitor-service/monitor.service';
import { ChqDropdownComponent } from 'src/app/widgets/chq-dropdown/chq-dropdown.component';
import { CellClickedCallback, SortChangedCallback, TableCellData, TableConfig, TableRowData } from 'src/app/widgets/chq-mat-table/chq-mat-table.component';
import { ApiPagination, PaginateInfo, defaultPagination } from 'src/app/widgets/chq-paginator/chq-paginator.component';
import { CustomerService } from 'src/app/services/customer/customer.service';
import { ActivatedRoute, Router } from '@angular/router';
import { CustomerCompanyTableConfig, CustomerTableConfig } from 'src/app/config/display/customer-table-config';
import { customerListMenus, menuItem } from 'src/app/model/menu';
import { CaseService } from 'src/app/services/case/case.service';
import { SeverityLevel } from '@microsoft/applicationinsights-web';
import { XaCurrencyPipe } from 'src/app/helper/pipe/xa-currency/xa-currency.pipe';
import { CalendarService } from 'src/app/services/calendar/calendar.service';

const ACTIVE_PATHS = {
  COMPANY: 'company',
  PERSONAL: 'individual'
}

@Component({
  selector: 'add-customer-table',
  templateUrl: './customer-table.component.html',
  styleUrls: [ './customer-table.component.scss' ],
  encapsulation: ViewEncapsulation.None,
})
export class CustomerTableComponent implements OnInit {

  // Personal Customer list variables
  public pageChanged: boolean = false;
  public tableConfig: TableConfig;
  public pagination: ApiPagination = defaultPagination;
  public static currentPage: number = 1;
  private readonly configData: any = CustomerTableConfig;
  public tableData: any;
  public textToSearch: string = '';

  // Filter variable
  @ViewChild('companyName') companyName: ChqDropdownComponent;
  @ViewChild('workProvider') workProvider: ChqDropdownComponent;
  public input: any = { personName: false, companyName: false };

  // Tabs Menu
  public customerListMenus = customerListMenus;
  public activeTab: string = ''
  public activeTabOptions = ACTIVE_PATHS

  // Company Customer list variables
  public pageChangedCompany: boolean = false;
  public tableConfigCompany: TableConfig;
  public paginationCompany: ApiPagination = defaultPagination;
  public static currentPageCompnay: number = 1;
  private readonly configDataCompany: any = CustomerCompanyTableConfig;
  public tableDataCompany: any;
  public workProviderFromBooking: Array<string> = [];
  public selectedCompanyForFilter = []

  // Extra Variables
  public dueAmountPersonClass = '';
  public dueAmountCompanyClass = '';
  public totalDueAmountCompany: any = 0;
  public totalDueAmountPerson:any = 0;
  public showTotalDueAmountCompany = false;
  public showTotalDueAmountPerson = false;
  public userDetail;
  private subscription: Subscription;
  public applyButtonModel: ChqWidgetsButtonModel = {
    label: 'apply',
    type: 'disabled',
    icon: '',
    onclick: this.applyFilter.bind(this)
  }
  public resetButtonModel: ChqWidgetsButtonModel = {
    label: 'reset_all',
    type: 'outline',
    icon: '',
    onclick: this.reSetFilter.bind(this)
  }

  // Company Filter
  public dropdownModelFilter: ChqWidgetsDropdownModel = {
    options: [ ],
    placeHolder: 'select_type',
    label: 'select_type',
    name: 'filterDropdown',
    displayValue: 'label',
    selectedOption: { label: '' }
  };

  public dropdownModelCompany: ChqWidgetsDropdownModel = {
    options: [],
    placeHolder: 'company_name',
    label: 'company_name',
    name: 'companyDropdown',
    displayValue: 'companyName',
    keyFieldName: 'CompanyId',
    selectedOption: [],
    isMultiSelect: true
  };

  //PERSONAL Filters
  public dropdownModelFilterPerson: ChqWidgetsDropdownModel = {
    options: [ { label: 'Retail - Individual' } ],
    placeHolder: 'select_type',
    label: 'select_type',
    name: 'filterDropdownPerson',
    displayValue: 'label',
    selectedOption: { label: '' }
  };

  public nameTextBox: ChqWidgetsInputModel = {
    placeHolder: 'customer_name',
    label: 'customer_name',
    name: 'name',
    type: 'text',
    value: '',
    required: false
  }

  i18n: i18nInfo;

  /**
   * constructor
   * @param monitorService
   * @param commonService
   * @param tableDataSer
   */
  constructor(
    private monitorService: MonitorService,
    private commonService: CommonService,
    private tableDataSer: CustomerService,
    private router: Router,
    private route: ActivatedRoute,
    private caseService: CaseService,
    private xaCurrencyPipe: XaCurrencyPipe,
    private calendarService: CalendarService,
  ) {
    this.i18n = this.commonService.geti18nInfo();
  }

  /**
   * on init
   */
  ngOnInit(): void {
    this.monitorService.logEvent('ngOnInit', [ 'CustomerTableComponent', 'addenda-repair' ]);
    this.tableConfig = this.configData;
    this.tableConfigCompany = this.configDataCompany

    this.route.params.subscribe(async (params) => {
      this.activeTab = params['userType'];
      if (this.activeTab === ACTIVE_PATHS.PERSONAL) {
        this.pagination = {
          ...this.pagination, ...{
            count: 0,
            currentPage: CustomerTableComponent.currentPage,
            orderBy: 'CreatedDate',
            sortDirection: 'desc'
          }
        };

        const paginationInfo: PaginateInfo = {
          currentPage: CustomerTableComponent.currentPage,
          offset: this.pagination.pageSize * (CustomerTableComponent.currentPage - 1),
          pageSize: this.pagination.pageSize
        }
        this.handlePaginationChange(paginationInfo);
        this.getTotalDueAmountPerson();
      } else {
        this.paginationCompany = {
          ...this.paginationCompany, ...{
            count: 0,
            currentPage: CustomerTableComponent.currentPageCompnay,
            orderBy: 'CreatedDate',
            sortDirection: 'desc'
          }
        };
        this.commonService.showLoading()
        const responseB = await firstValueFrom(this.tableDataSer.getCompanyWorkProviders('$apply=groupby((CompanyOrgType))'));
        if (responseB && responseB.length > 0) {
          this.workProviderFromBooking = responseB.map(ele => ele.CompanyOrgType)
        }
        this.getCompanyCustomers();
        this.getTotalDueAmountCompany()
        if (!this.dropdownModelFilter.observable) {
          this.dropdownModelFilter = {
            ...this.dropdownModelFilter,
            observable: this.caseService.getProviders(),
            onObservableLoad: this.onObservableLoadWorkProviderData.bind(this)
          }
          this.workProvider.dropdownModel.observable = this.caseService.getProviders();
          this.workProvider.getObservable();
        }
      }
    });
    this.subscription = this.commonService.userProfileData.subscribe((res) => {
      this.userDetail = res?.data;
    })
  }

  /**
    @returns {void} ngOnDestroy for GenerateInvoicesComponent
  */
  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  /**
    Retrieves a list of customers for a company.
    @returns {Promise} A promise that resolves with the list of customers.
  */
  public async getCompanyCustomers(): Promise<any> {
    this.commonService.showLoading()
    let queryParams = '';
    if(this.selectedCompanyForFilter?.length > 0) {
      queryParams = `$filter=(${this.selectedCompanyForFilter.map((x) => { return `ContactId eq ${x.CompanyId}` }).join(' or ')})`;
    }
    return firstValueFrom(this.tableDataSer.getCompanyCustomers(this.paginationCompany, queryParams)).then((response) => {
      const result = response.value;
      result.map((ele) => {
        let dueAmountClass = '';
        ele.AmountDue = ele.Amount - ele.AmountReceived;
        if (ele.Amount && ele.AmountDue !== undefined) {
          const dueAmountPrecent = (ele.AmountDue * 100) / ele.Amount
          if (dueAmountPrecent >= 30) dueAmountClass = 'color-xa-dark-red';
          if (dueAmountPrecent <= 10 && dueAmountPrecent > 0) dueAmountClass = 'col-xa-orange2';
          if (dueAmountPrecent == 0) dueAmountClass = 'color-xa-repair-header';
        }
        ele.FormattedTotalAmount = ele.Amount ? this.xaCurrencyPipe.transform(ele.Amount, this.i18n.currencySymbol, this.i18n.digitsInfo, this.i18n.locale, this.i18n.position) : '-'
        ele.FormattedPaidAmount = ele.AmountReceived ? this.xaCurrencyPipe.transform(ele.AmountReceived, this.i18n.currencySymbol, this.i18n.digitsInfo, this.i18n.locale, this.i18n.position) : '-'
        ele.FormattedDueAmount = ele.Amount ?
          '<span class="' + dueAmountClass + '">' + this.xaCurrencyPipe.transform(ele.AmountDue, this.i18n.currencySymbol, this.i18n.digitsInfo, this.i18n.locale, this.i18n.position) : '-' + '</span>'
        ele.FormattedCompanyName = ele.CompanyName ? `<span class="company-name">${ele.CompanyName}${ele.LicenseNumber ? '-<i>' + ele.LicenseNumber + '</i>': ''}</span>` : ''
      })
      this.tableDataCompany = result;
      this.paginationCompany = { ...this.paginationCompany, count: response['@odata.count'] || 0 };
      this.commonService.hideLoading();
    }).catch((err) => {
      this.monitorService.logException(err, SeverityLevel.Error);
      this.commonService.hideLoading();
    });
  }


  /**
    Retrieves customer data from a table data service and sets it to the table data property.
    If an error occurs, the apply button type is set to primary and the loading spinner is hidden.
    @returns {Promise} A promise that resolves to the customer data or rejects with an error.
  */
  public async getCustomers(): Promise<any> { // individual customer list
    const selectedFilter = this.dropdownModelFilterPerson.selectedOption?.label;
    let queryParams = '';
    switch (selectedFilter?.toLowerCase()) {
    case 'retail - individual':
      if (this.textToSearch != '') {
        // To Fix typescript error convert the value to string by appending ''
        const empName = encodeURIComponent(this.textToSearch.toString())
        queryParams = `$filter=(contains(tolower(FirstName),'${empName.toLowerCase()}')) or (contains(tolower(LastName),'${empName.toLowerCase()}'))`;
      }
      break;
    default:
      break;
    }

    this.commonService.showLoading();
    return firstValueFrom(this.tableDataSer.getCustomers(this.pagination, queryParams)).then((response) => {
      const result = response.value;
      result.map((ele) => {
        let dueAmountClass = '';
        ele.AmountDue = ele.Amount - ele.AmountReceived;
        if (ele.Amount && ele.AmountDue !== undefined) {
          const dueAmountPrecent = (ele.AmountDue * 100) / ele.Amount
          if (dueAmountPrecent >= 30) dueAmountClass = 'color-xa-dark-red';
          if (dueAmountPrecent <= 10 && dueAmountPrecent > 0) dueAmountClass = 'col-xa-orange2';
          if (dueAmountPrecent == 0) dueAmountClass = 'color-xa-repair-header';
        }
        ele.FormattedTotalAmount = ele.Amount ? this.xaCurrencyPipe.transform(ele.Amount, this.i18n.currencySymbol, this.i18n.digitsInfo, this.i18n.locale, this.i18n.position) : '-'
        ele.FormattedPaidAmount = ele.AmountReceived ? this.xaCurrencyPipe.transform(ele.AmountReceived, this.i18n.currencySymbol, this.i18n.digitsInfo, this.i18n.locale, this.i18n.position) : '-'
        ele.FormattedDueAmount = ele.Amount && ele.AmountDue !== undefined ?
          '<span class="' + dueAmountClass + '">' + this.xaCurrencyPipe.transform(ele.AmountDue, this.i18n.currencySymbol, this.i18n.digitsInfo, this.i18n.locale, this.i18n.position) + '</span>' : '-'
      })
      this.tableData = result;
      this.pagination = { ...this.pagination, count: response['@odata.count'] || 0 };
      this.commonService.hideLoading();
    }).catch((err) => {
      this.monitorService.logException(err, SeverityLevel.Error);
      this.commonService.hideLoading();
    });
  }

  /**
    @returns {Promise} A promise that resolves to the customer data or rejects with an error.
  */
  public async getTotalDueAmountCompany(): Promise<any> {
    this.showTotalDueAmountCompany = false
    let query = '$apply=';
    if(this.selectedCompanyForFilter?.length > 0) {
      query += `filter(${this.selectedCompanyForFilter.map((x) => { return `ContactId eq ${x.CompanyId}` }).join(' or ')})/`;
    }
    query += 'aggregate(Amount with sum as TotalAmount, AmountReceived with sum as TotalAmountReceived)'
    return firstValueFrom(this.tableDataSer.getTotalDueAmountCompany(query)).then((response: any) => {
      if(response && response[0]) {
        this.showTotalDueAmountCompany = true
        this.dueAmountCompanyClass = ''
        this.totalDueAmountCompany = response[0].TotalAmount ? response[0].TotalAmount - response[0].TotalAmountReceived : '-';
        if(response[0].TotalAmount) {
          const dueAmountPrecent = (this.totalDueAmountCompany * 100) / response[0].TotalAmount;
          if (dueAmountPrecent >= 30) this.dueAmountCompanyClass = 'color-xa-dark-red';
          if (dueAmountPrecent <= 10 && dueAmountPrecent > 0) this.dueAmountCompanyClass = 'col-xa-orange2';
          if (dueAmountPrecent == 0) this.dueAmountCompanyClass = 'color-xa-repair-header';
        }
      }
    }).catch((err) => {
      this.showTotalDueAmountCompany = true
      this.monitorService.logException(err, SeverityLevel.Error);
    });
  }

  /**
    @returns {Promise} A promise that resolves to the customer data or rejects with an error.
  */
  public async getTotalDueAmountPerson(): Promise<any> {
    this.showTotalDueAmountPerson = false
    let query = '$apply=';
    const selectedFilter = this.dropdownModelFilterPerson.selectedOption?.label;
    switch (selectedFilter?.toLowerCase()) {
    case 'retail - individual':
      if (this.textToSearch != '') {
        // To Fix typescript error convert the value to string by appending ''
        const empName = encodeURIComponent(this.textToSearch.toString())
        query += `filter(contains(tolower(FirstName),'${empName.toLowerCase()}') or contains(tolower(LastName),'${empName.toLowerCase()}'))/`;
      }
      break;
    default:
      break;
    }

    query += 'aggregate(Amount with sum as TotalAmount, AmountReceived with sum as TotalAmountReceived)'
    return firstValueFrom(this.tableDataSer.getTotalDueAmountPerson(query)).then((response: any) => {
      if(response && response[0]) {
        this.showTotalDueAmountPerson = true;
        this.dueAmountPersonClass = ''
        this.totalDueAmountPerson = response[0].TotalAmount ? response[0].TotalAmount - response[0].TotalAmountReceived : '-';
        if(response[0].TotalAmount) {
          const dueAmountPrecent = (this.totalDueAmountPerson * 100) / response[0].TotalAmount;
          if (dueAmountPrecent >= 30) this.dueAmountPersonClass = 'color-xa-dark-red';
          if (dueAmountPrecent <= 10 && dueAmountPrecent > 0) this.dueAmountPersonClass = 'col-xa-orange2';
          if (dueAmountPrecent == 0) this.dueAmountPersonClass = 'color-xa-repair-header';
        }
      }
    }).catch((err) => {
      this.showTotalDueAmountPerson = true
      this.monitorService.logException(err, SeverityLevel.Error);
    });
  }

  /**
    Handles a change in the sorting of the customer data.
    @param {Sort} sort - The new sorting criteria.
    @returns {void}
  */
  handleSortChange: SortChangedCallback = (sort: Sort): void => {
    if (sort) {
      this.pagination = { ...this.pagination, ...{ orderBy: sort.active, sortDirection: sort.direction } };
      this.getCustomers()
    }
  }

  /**
    Handles a change in the sorting of company customers.
    @param {Sort} sort - The new sorting criteria.
    @returns {void}
  */
  handleSortChangeCompany: SortChangedCallback = (sort: Sort): void => {
    if (sort) {
      this.paginationCompany = { ...this.paginationCompany, ...{ orderBy: sort.active, sortDirection: sort.direction } };
      this.getCompanyCustomers()
    }
  }

  /**
   * page change
   * @param event
   */
  handlePaginationChange(event: PaginateInfo): void {
    const pagination: ApiPagination = {
      pageSize: event.pageSize,
      offset: event.offset,
      currentPage: event.currentPage
    };

    this.pagination.currentPage = event.currentPage;
    this.pagination = { ...this.pagination, ...pagination };

    this.pageChanged = true;
    this.getCustomers();
  }


  /**
   * page change
   * @param event
   */
  handlePaginationChangeCompany(event: PaginateInfo): void {
    const pagination: ApiPagination = {
      pageSize: event.pageSize,
      offset: event.offset,
      currentPage: event.currentPage
    };

    this.paginationCompany.currentPage = event.currentPage;
    this.paginationCompany = { ...this.paginationCompany, ...pagination };

    this.pageChangedCompany = true;
    this.getCompanyCustomers();
  }

  /**
   * selected any filter in the dropdown
   * @param $event
   */
  public selectFilter($event: any): void {
    this.monitorService.logEvent('selectFilter', [ 'CustomerTableComponent', 'addenda-repair', {
      $event
    } ]);
    if ($event?.name == 'filterDropdown') {
      const query = `?$filter=( OrgId eq ${this.userDetail?.organizationDetail?.id} and CompanyOrgType eq '${$event.value.label}' and IsIndividual eq false)`;
      this.dropdownModelFilter.selectedOption = $event.value;
      this.hideShowDropdown('companyName');
      this.dropdownModelCompany = {
        ...this.dropdownModelCompany,
        selectedOption: [],
        options: [],
        displayValue: 'label',
        observable: this.calendarService.getCustomer(query),
        onObservableLoad: this.onObservableLoadCompanyData.bind(this)
      }
      setTimeout(() => {
        this.companyName?.getObservable()
      }, 100)
      this.updateApplyButtonState()
    } else if ($event.name == 'filterDropdownPerson') {
      this.dropdownModelFilterPerson.selectedOption = $event.value
      switch ($event.value[this.dropdownModelFilterPerson.displayValue]?.toLowerCase()) {
      case 'retail - individual':
        this.hideShowDropdown('personName');
        this.nameTextBox.value = ''
        break;
      default:
        this.hideShowDropdown('');
        break;
      }
    } else if ($event?.name == 'companyDropdown') {
      this.dropdownModelCompany.selectedOption = $event.value;
      this.updateApplyButtonState()
    }
  }


  /**
   *
   * @param event
   */
  public updateInput(event: IControlValue): void {
    if (event.name == 'name') {
      this.nameTextBox.value = event.value;
      this.updateApplyButtonState()
    }
  }

  /**
   *
   * @param event
   */
  public updateApplyButtonState(): void {
    let state: 'disabled' | 'secondary' = 'disabled';
    if (this.activeTab === this.activeTabOptions.PERSONAL) {
      if (this.nameTextBox.value && this.nameTextBox.value.toString().length >= 3) {
        state = 'secondary'
      }
    } else if (this.activeTab === this.activeTabOptions.COMPANY) {
      if (this.dropdownModelCompany.selectedOption?.length > 0 && this.input.companyName) {
        state = 'secondary'
      }
    }
    this.applyButtonModel.type = state
  }

  /**
   * hide show dropdown
  */
  private hideShowDropdown(key: string): void {
    this.input[key] = true;
    Object.keys(this.input).forEach(
      (value) => {
        if (value != key) {
          this.input[value] = false;
        }
      }
    )
  }

  /**
    Handles a cell click event.
    @param {TableRowData} row - The row data.
    @param {Partial} column - The column data.
    @returns {void}
  */
  handleCellClick: CellClickedCallback = (row: TableRowData, column: Partial<TableCellData>): void => {
    if(column.id === 'ContactId') {
      const customerId = row.find(ele => ele.id === column.id)?.result?.value;
      if(customerId) {
        const customerDetails = this.tableData.find(ele => ele.ContactId === customerId)
        if(customerDetails) this.commonService.customerDetails.next(customerDetails);
        if(customerId) this.router.navigate([ `/repair/customers/individual/${customerId}/${customerDetails.Id}/repair-order` ])
      }

    }
  };

  /**
    Handles a cell click event for a company column.
    @param {TableRowData} row - The row data for the clicked cell.
    @param {Partial} column - The column data for the clicked cell.
    @returns {void}
  */
  handleCellClickCompany: CellClickedCallback = (row: TableRowData, column: Partial<TableCellData>): void => {
    if(column.id === 'ContactId') {
      const companyId = row.find(ele => ele.id === column.id)?.result?.value
      if(companyId) {
        const customerDetails = this.tableDataCompany.find(ele => ele.ContactId === companyId)
        if(customerDetails) this.commonService.customerDetails.next(customerDetails);
        if(companyId) this.router.navigate([ `/repair/customers/company/${companyId}/${customerDetails.Id}/repair-order` ])
      }

    }
  };

  /**
    Navigates to the specified menu item's URL using the Angular Router.
    @param {menuItem} item - The menu item to navigate to.
    @returns {void}
  */
  navigate(item: menuItem): void {
    const url = item.url;
    this.router.navigate([ url ]);
    if(this.dropdownModelFilter?.selectedOption) {
      this.input.companyName = false
      this.dropdownModelFilter = { ...this.dropdownModelFilter, selectedOption: {} }
    }
    if(this.dropdownModelCompany?.selectedOption) {
      this.dropdownModelCompany = { ...this.dropdownModelCompany, selectedOption: [] }
    }
    if(this.dropdownModelFilterPerson?.selectedOption) {
      this.input.personName = false;
      this.dropdownModelFilterPerson = { ...this.dropdownModelFilterPerson, selectedOption: {} }
    }
    if(this.nameTextBox?.value) {
      this.nameTextBox = { ...this.nameTextBox, value: '' }
    }
  }

  /**
    Applies a filter based on the active tab selected by the user.
    If the active tab is "COMPANY", it calls the "getCompanyCustomers" function.
    If the active tab is "PERSONAL", it calls the "getCustomers" function.
    @returns {void}
  */
  applyFilter(): void {
    if (this.activeTab === this.activeTabOptions.COMPANY) {
      this.paginationCompany = { ...this.paginationCompany, currentPage: 1 }
      this.selectedCompanyForFilter = [ ...this.dropdownModelCompany.selectedOption ]
      this.getCompanyCustomers();
      this.getTotalDueAmountCompany();
    } else if (this.activeTab === this.activeTabOptions.PERSONAL) {
      this.pagination = { ...this.pagination, currentPage: 1 }
      this.textToSearch = (this.nameTextBox.value + '').trim()
      this.getCustomers()
      this.getTotalDueAmountPerson()
    }
  }
  /**
    Reset a filter based on the active tab selected by the user.
    If the active tab is "COMPANY", it calls the "getCompanyCustomers" function.
    If the active tab is "PERSONAL", it calls the "getCustomers" function.
    @returns {void}
  */
  reSetFilter(): void {
    if (this.activeTab === this.activeTabOptions.COMPANY) {
      this.dropdownModelCompany = { ...this.dropdownModelCompany, selectedOption: [] }
      this.dropdownModelFilter = { ...this.dropdownModelFilter, selectedOption: {} }
      this.paginationCompany = { ...this.paginationCompany, currentPage: 1 }
      this.input.companyName = false;
      this.selectedCompanyForFilter = []
      this.updateApplyButtonState()
      this.getCompanyCustomers()
      this.getTotalDueAmountCompany();
    } else if (this.activeTab === this.activeTabOptions.PERSONAL) {
      this.nameTextBox = { ...this.nameTextBox, value: '' }
      this.pagination = { ...this.pagination, currentPage: 1 }
      this.dropdownModelFilterPerson = { ...this.dropdownModelFilterPerson, selectedOption: {} }
      this.input.personName = false;
      this.textToSearch = ''
      this.getCustomers()
      this.getTotalDueAmountPerson()
    }
    this.updateApplyButtonState();
  }

  /**
    @param {any} items - The compnay items to update the form model with.
    @returns {void}
  */
  onObservableLoadCompanyData(items: any): void {
    if (items && items.length > 0) {
      items.forEach((item, i) => {
        items[i] = {
          ...items[i],
          label: `${item.CompanyName}${item.LicenseNumber ? '-' + item.LicenseNumber : ''}`,
          html: `<span class="company-name-dropdown">${item.CompanyName}${item.LicenseNumber? '-<i>' + item.LicenseNumber + '</i>': ''}</span>`,
        }
      });
      this.dropdownModelCompany = { ...this.dropdownModelCompany, options: items, selectedOption: items }
      this.updateApplyButtonState()
    }
  }

  /**
    @param {any} items - The compnay items to update the form model with.
    @returns {void}
  */
  onObservableLoadWorkProviderData(items: any): void {
    if (items && items.length > 0) {
      const filteredItems = items.filter((item, i) => {
        return this.workProviderFromBooking.indexOf(item.label) !== -1 ? true : false
      });
      this.workProvider.dropdownOptions = filteredItems;
    }
  }
}
