import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { ApiPagination } from 'src/app/widgets/chq-paginator/chq-paginator.component';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {
  /**
   * constructor
   * @param http 
   */
  constructor(
    private http: HttpClient
  ) { }

  /**
   * 
   * @param getCustomers  
   * @returns observable
   * fake data have to remove once api is implemented
   */
  public getCustomers(pagination: ApiPagination, queryParams: string = null): Observable<any> {
    const skip = (pagination.currentPage - 1) * pagination.pageSize;
    queryParams = (!queryParams) ? `$top=${pagination.pageSize}&$skip=${skip}` : `$top=${pagination.pageSize}&$skip=${skip}&${queryParams}`
    const orderBy = `&$orderby=${pagination.orderBy} ${pagination.sortDirection}`
    const url = `${environment.caseMgmtUrl}/odata/customerinvoice?${queryParams}${orderBy}&count=true`;
    return this.http.get<any>(url);
  }

  /**
   * 
   * @param   getCompanyCustomers
   * @returns observable
   * fake data have to remove once api is implemented
   */
  public getCompanyCustomers(pagination: ApiPagination, queryParams=''): Observable<any> {
    const skip = (pagination.currentPage - 1) * pagination.pageSize;
    queryParams = (!queryParams) ? `$top=${pagination.pageSize}&$skip=${skip}` : `$top=${pagination.pageSize}&$skip=${skip}&${queryParams}`
    const orderBy = `&$orderby=${pagination.orderBy} ${pagination.sortDirection}`
    const url = `${environment.caseMgmtUrl}/odata/companyinvoice?${queryParams}${orderBy}&count=true`;
    return this.http.get<any>(url);
  }

  /**
   * 
   * @param   getCompanyCustomers
   * @returns observable
   * fake data have to remove once api is implemented
   */
  public getCompanyWorkProviders(queryParams=''): Observable<any> {
    const url = `${environment.caseMgmtUrl}/odata/companyinvoice?${queryParams}`;
    return this.http.get<any>(url);
  }


  /**
    Retrieves repair orders from a mock data source.
    @param {ApiPagination} pagination - The pagination object containing the page number and page size.
    @param {string} [queryParams] - Optional query parameters to filter the results.
    @returns {Observable} - An observable that emits the retrieved repair orders.
  */
  public getReapirOrders(customerId: string, userType:string, pagination: ApiPagination): Observable<any> {
    const skip = (pagination.currentPage - 1) * pagination.pageSize;
    const queryParams = `$top=${pagination.pageSize}&$skip=${skip}`
    const orderBy = `&$orderby=${pagination.orderBy} ${pagination.sortDirection}`
    let filter = ''
    if(userType === 'company') {
      filter = `&$filter=(companyId eq ${customerId})&companyIndividualType=1`
    } else {
      filter = `&$filter=(contactId eq ${customerId})&companyIndividualType=2`
    }
    // const filter = ''
    const url = `${environment.caseMgmtUrl}/odata/repairorder?${queryParams}${orderBy}${filter}&count=true`;
    return this.http.get<any>(url);
  }

  /**
    Retrieves a list of cases based on the provided pagination and query parameters.
    @param {ApiPagination} pagination - An object containing information about the pagination to be applied to the results.
    @param {string} [queryParams] - An optional string containing any additional query parameters to be included in the request.
    @returns {Observable} - An Observable that emits the retrieved list of cases.
  */
  public getCases(customerId: string, userType:string, pagination: ApiPagination): Observable<any> {
    const skip = (pagination.currentPage - 1) * pagination.pageSize;
    const queryParams = `$top=${pagination.pageSize}&$skip=${skip}`
    const orderBy = `&$orderby=${pagination.orderBy} ${pagination.sortDirection}`
    let filter = '';
    if(userType === 'company') {
      filter = `&$filter=(companyId eq ${customerId})&companyIndividualType=1`
    } else {
      filter = `&$filter=(contactId eq ${customerId})&companyIndividualType=2`
    }
    const url = `${environment.caseMgmtUrl}/odata/repairestimation?${queryParams}${orderBy}${filter}&count=true`;
    return this.http.get<any>(url);
  }
  
  /**
    Retrieves total due amount compnay, provided query parameters.
    @param {string} [queryParams] - An optional string containing any additional query parameters to be included in the request.
    @returns {Observable} - An Observable that emits the retrieved list of cases.
  */
  public getTotalDueAmountCompany(queryParams = ''): Observable<any> {
    return this.http.get<any>(`${environment.caseMgmtUrl}/odata/companyinvoice?${queryParams}`);
  }

  /**
    Retrieves total due amount compnay, provided query parameters.
    @param {string} [queryParams] - An optional string containing any additional query parameters to be included in the request.
    @returns {Observable} - An Observable that emits the retrieved list of cases.
  */
  public getTotalDueAmountPerson(queryParams = ''): Observable<any> {
    return this.http.get<any>(`${environment.caseMgmtUrl}/odata/customerinvoice?${queryParams}`);
  }

  /**
    Retrieves company details
    @returns {Observable} - An Observable that emits the retrieved list of cases.
  */
  public getCompanyDetails(companyId: any): Observable<any> {
    return this.http.get<any>(`${environment.caseMgmtUrl}/api/companyinvoice/${companyId}`);
  }

  /**
    Retrieves Customer details
    @returns {Observable} - An Observable that emits the retrieved list of cases.
  */
  public getCustomerDetails(customerId: any): Observable<any> {
    return this.http.get<any>(`${environment.caseMgmtUrl}/api/customerinvoice/${customerId}`);
  }
}
