import { Component } from '@angular/core';
import { CellClickedCallback, SortChangedCallback, TableCellData, TableConfig, TableRowData } from 'src/app/widgets/chq-mat-table/chq-mat-table.component';
import { Sort } from '@angular/material/sort';
import { firstValueFrom } from 'rxjs';
import { ApiPagination, defaultPagination, PaginateInfo } from 'src/app/widgets/chq-paginator/chq-paginator.component';
import { CustomerService } from 'src/app/services/customer/customer.service';
import { CommonService, i18nInfo } from 'src/app/services/common/common.service';
import { ReapirTableConfig, CasesTableConfig } from 'src/app/config/display/customer-details-config';
import { ActivatedRoute, Router } from '@angular/router';
import { customerDetailsMenus, menuItem } from 'src/app/model/menu';
import { formatCurrency, formatNumber } from '@angular/common';
import { SeverityLevel } from '@microsoft/applicationinsights-web';
import { MonitorService } from 'src/app/services/monitor-service/monitor.service';
import { XaCurrencyPipe } from 'src/app/helper/pipe/xa-currency/xa-currency.pipe';

import { CaseStatusMapper, ReapirStatusMapper } from 'src/app/config/route-mapper/reapir-status-mapper';
const PATHS = {
  repair: 'repair-order',
  cases: 'estimates'
}

const caseStatusMapper = CaseStatusMapper

const reapirStatusMapper = ReapirStatusMapper

@Component({
  selector: 'customer-details',
  templateUrl: './customer-details.component.html',
  styleUrls: [ './customer-details.component.scss' ]
})
export class CustomerDetailsComponent {
  panelOpenState = false;
  public tableConfigRepair: TableConfig;
  public tableDataRepair: any;
  public pageChangedRepair: boolean = false;
  public paginationRepair: ApiPagination = defaultPagination;
  public static currentPageRepair: number = 1;
  public totalCountRepair: number = 0;
  private readonly configDataRepair: any = ReapirTableConfig;


  public tableConfigCase: TableConfig;
  public tableDataCase: any;
  public pageChangedCase: boolean = false;
  public paginationCase: ApiPagination = defaultPagination;
  public static currentPageCase: number = 1;
  public totalCountCase: number = 0;
  private readonly configDataCase: any = CasesTableConfig;


  public userDetails: any = {};
  public customerId: string = '';
  public uniqueId: string = '';
  public userType: string = '';
  public haveQuotePermissions: boolean = false;
  public userDetailsLoading: boolean = false;

  // Tabs Menu
  public customerDetailsMenus = customerDetailsMenus;
  public activeTab: string = ''
  public tabOptions = PATHS;
  
  i18n: i18nInfo;

  /**
    Constructor for the CustomerService class.
    @constructor
    @param {CustomerService} customerService - The CustomerService instance to be used in the constructor.
    @param {CommonService} commonService - The CommonService instance to be used in the constructor.
    @param {Router} router - The Router instance.
  */
  constructor(
    private monitorService: MonitorService,
    private customerService: CustomerService,
    private route: ActivatedRoute,
    private commonService: CommonService,
    private router: Router,
    private xaCurrencyPipe: XaCurrencyPipe) {
    this.i18n = this.commonService.geti18nInfo();
  }

  /**
    Lifecycle hook that is called after the component is initialized.
    @returns {Promise} A promise that resolves when the initialization is complete.
  */
  async ngOnInit(): Promise<void> {
    this.monitorService.logEvent('ngOnInit', [ 'CustomerDetailsComponent', 'addenda-repair' ]);

    this.commonService.userProfileData.subscribe((res: any) => {
      const automotiveServices = res?.data?.userPermission?.automotiveServices || []; 
      this.haveQuotePermissions = automotiveServices.find(ele => ele.automotiveServiceName === 'Carheal Quote')
    })

    this.route.params.subscribe((params) => {
      this.customerId = params['contactId'];
      this.uniqueId = params['id'];
      this.userType = params['userType']; 

      this.activeTab = params['orderType'];
      this.tableConfigRepair = this.configDataRepair;
      this.tableConfigCase = this.configDataCase;

      this.paginationRepair = {
        ...this.paginationRepair, ...{
          count: this.totalCountRepair,
          currentPage: CustomerDetailsComponent.currentPageRepair,
          orderBy: 'CreatedDate',
          sortDirection: 'desc'
        }
      };
      this.paginationCase = {
        ...this.paginationCase, ...{
          count: this.totalCountCase,
          currentPage: CustomerDetailsComponent.currentPageCase,
          orderBy: 'CreatedDate',
          sortDirection: 'desc'
        }
      };
      if(this.activeTab === PATHS.repair) this.getRepairOrders();
      else this.getCases();
      this.getUserDetails();
    });

    
  }

  /**
    Get Customer details
    @returns {Promise} A promise that resolves when the repair orders are retrieved and the table data is updated.
  */
  public async getUserDetails(): Promise<any> {
    if(this.userType === 'company') {
      this.userDetailsLoading = true;
      return firstValueFrom(this.customerService.getCompanyDetails(this.customerId)).then((response) => {
        if(response?.data) {
          this.userDetails = response.data;
          if(!this.userDetails.amount) this.userDetails.amount = 0
          if(!this.userDetails.amountReceived) this.userDetails.amountReceived = 0
          if(!this.userDetails.amountDue) this.userDetails.amountDue = 0
          this.userDetailsLoading = false;
        }
      }).catch((err) => {
        this.monitorService.logException(err, SeverityLevel.Error);
      });
    } else {
      this.userDetailsLoading = true;
      return firstValueFrom(this.customerService.getCustomerDetails(this.uniqueId)).then((response) => {
        if(response?.data) {
          this.userDetails = response.data;
          this.userDetailsLoading = false;
        }
      }).catch((err) => {
        this.monitorService.logException(err, SeverityLevel.Error);
      });
    }
  }

  /**
    Retrieves repair orders from the customer service and updates the table data for the repair orders.
    @returns {Promise} A promise that resolves when the repair orders are retrieved and the table data is updated.
  */
  public async getRepairOrders(): Promise<any> {
    this.commonService.showLoading();
    return firstValueFrom(this.customerService.getReapirOrders(this.customerId, this.userType, this.paginationRepair)).then((response) => {
      const result = response.value
      result.map((ele) => {
        ele.AmountDue = ele.TotalInvoiceAmount - ele.PaidAmount;
        ele.FormattedTotalAmount = ele.TotalInvoiceAmount ? this.xaCurrencyPipe.transform(ele.TotalInvoiceAmount, this.i18n.currencySymbol, this.i18n.digitsInfo, this.i18n.locale, this.i18n.position) : '-'
        ele.FormattedPaidAmount = ele.PaidAmount ? this.xaCurrencyPipe.transform(ele.PaidAmount, this.i18n.currencySymbol, this.i18n.digitsInfo, this.i18n.locale, this.i18n.position) : '-'
        ele.FormattedDueAmount = ele.TotalInvoiceAmount ? this.xaCurrencyPipe.transform(ele.AmountDue, this.i18n.currencySymbol, this.i18n.digitsInfo, this.i18n.locale, this.i18n.position) : '-'
      })
      this.tableDataRepair = result;
      this.paginationRepair = { ...this.paginationRepair, count: response['@odata.count'] || 0 };
      this.commonService.hideLoading();
    }).catch((err) => {
      this.monitorService.logException(err, SeverityLevel.Error);
      this.commonService.hideLoading();
    });
  }

  /**
    Retrieves cases from the customer service and updates the table data with the response.
    @returns {Promise} A promise that resolves when the cases are retrieved and the table data is updated.
  */
  public async getCases(): Promise<any> {
    this.commonService.showLoading();
    return firstValueFrom(this.customerService.getCases(this.customerId, this.userType, this.paginationCase)).then((response) => {
      const result = response.value
      result.map((ele) => {
        ele.FormattedTotalEstimateAmount = ele.TotalEstimateAmount ? this.xaCurrencyPipe.transform(ele.TotalEstimateAmount, this.i18n.currencySymbol, this.i18n.digitsInfo, this.i18n.locale, this.i18n.position) : '-'
      })
      this.tableDataCase = result;
      this.paginationCase = { ...this.paginationCase, count: response['@odata.count'] || 0 };
      this.commonService.hideLoading();
    }).catch((err) => {
      this.monitorService.logException(err, SeverityLevel.Error);
      this.commonService.hideLoading();
    });
  }

  /**
    @description Handles a cell click event for a repair table.
    @param {TableRowData} row - The row data for the clicked cell.
    @param {Partial} column - The column data for the clicked cell.
    @returns {void}
  */
  handleCellClickRepair: CellClickedCallback = (row: TableRowData, column: Partial<TableCellData>): void => {
    if(column.id === 'Id') {
      const id = row.find(ele => ele.id === column.id)?.result?.value;
      if(id) {
        const clickedRawData = this.tableDataRepair.find(ele => ele.RepairNumber === id)
        if(clickedRawData) {
          const routeToRedirect = reapirStatusMapper[clickedRawData.Status.toLowerCase()] || reapirStatusMapper.others;
          this.router.navigate([ routeToRedirect.replace('{{repairGUID}}', clickedRawData.RepairGuid) ]);
        }
      }
    }
  }

  /**
    @description Handles a cell click event for a case.
    @param {TableRowData} row - The row data for the clicked cell.
    @param {Partial} column - The column data for the clicked cell.
    @returns {void}
  */
  handleCellClickCase: CellClickedCallback = (row: TableRowData, column: Partial<TableCellData>): void => {
    if(column.id === 'Id') {
      const id = row.find(ele => ele.id === column.id)?.result?.value;
      if(id) {
        const clickedRawData = this.tableDataCase.find(ele => ele.EstimateNumber === id)
        if(clickedRawData) {
          const routeToRedirect = caseStatusMapper[clickedRawData.EstimationStatus.toLowerCase()] || caseStatusMapper.others;
          this.router.navigate([ routeToRedirect.replace('{{caseGUID}}', clickedRawData.CaseGuid) ]);
        }
      }
    }
  }

  /**
    A function that handles a change in the sorting of data for a repair table.
    @param {Sort} sort - The new sorting criteria.
    @returns {void}
  */
  handleSortChangeRepair: SortChangedCallback = (sort: Sort): void => {
    if (sort) {
      this.paginationRepair = { ...this.paginationRepair, ...{ orderBy: sort.active, sortDirection: sort.direction } };
      this.getRepairOrders()
    }
  }

  /**
    @description Handles a change in the sorting of data for a case table.
    @param {Sort} sort - The new sorting criteria.
    @returns {void}
  */
  handleSortChangeCase: SortChangedCallback = (sort: Sort): void => {
    if (sort) {
      this.paginationCase = { ...this.paginationCase, ...{ orderBy: sort.active, sortDirection: sort.direction } };
      this.getCases()
    }
  }

  /**
   * handlePaginationChange
   * @param event
   */
  handlePaginationChangeReapir(event: PaginateInfo): void {
    const pagination: ApiPagination = {
      pageSize: event.pageSize,
      offset: event.offset,
      currentPage: event.currentPage
    };

    this.paginationRepair.currentPage = event.currentPage;
    this.paginationRepair = { ...this.paginationRepair, ...pagination };

    this.pageChangedRepair = true;
    this.getRepairOrders()
  }

  /**
   * handlePaginationChange
   * @param event
   */
  handlePaginationChangeCase(event: PaginateInfo): void {
    const pagination: ApiPagination = {
      pageSize: event.pageSize,
      offset: event.offset,
      currentPage: event.currentPage
    };

    this.paginationCase.currentPage = event.currentPage;
    this.paginationCase = { ...this.paginationCase, ...pagination };

    this.pageChangedCase = true;
    this.getCases()
  }

  /**
    Navigates to the specified menu item's URL using the Angular Router.
    @param {menuItem} item - The menu item to navigate to.
    @returns {void}
  */
  navigate(item:menuItem): void{
    const url = item.url;
    if(this.userType && this.customerId) {
      this.router.navigate([ url.replace('{userType}', this.userType).replace('{customerId}', this.customerId).replace('{id}', this.uniqueId) ]);
    }
    
  }

  /**
    Navigates to the customer listing based on usertype using the Angular Router.
    @returns {void}
  */
  handleBackClick(): void{
    this.router.navigate([ '/repair/customers/{userType}'.replace('{userType}', this.userType) ]);
  }
}
