import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';
import { Route, RouterModule } from '@angular/router';
import { MatIconModule } from '@angular/material/icon';
import { MatMenuModule } from '@angular/material/menu';
import { IconsModule } from 'src/app/icons.module';
import { WidgetsModule } from 'src/app/widgets/chq-widgets.module';
import { CustomerTableComponent } from './customer-list/customer-table.component';
import { CustomerDetailsComponent } from './customer-details/customer-details.component';
import { MatExpansionModule } from '@angular/material/expansion';
import { TranslateModule } from '@ngx-translate/core';
import { SharedModule } from 'src/app/shared/shared.module';

const route: Route[] = [
  {
    path: ':userType',
    component: CustomerTableComponent,
    resolve: { version: VersionControlResolver }
  },
  {
    path: ':userType/:contactId/:id/:orderType',
    component: CustomerDetailsComponent,
    resolve: { version: VersionControlResolver }
  },
];

@NgModule({
  declarations: [ CustomerTableComponent, CustomerDetailsComponent ],
  imports: [
    CommonModule,
    MatIconModule,
    IconsModule,
    RouterModule.forChild(route),
    MatMenuModule,
    WidgetsModule,
    MatExpansionModule,
    TranslateModule,
    SharedModule
  ]
})
export class CustomerTableModule { }
